<template>
  <div class='forumListCon'>
    <hr />
    <template v-if="forumLists.length>0">
      <div class='forumList' v-for='item in forumLists' :key="item.discussId">
        <div class='userInfo'>
          <div class="headImg" :style="{backgroundImage:'url('+item.headimg+')'}"></div>
          <div class='InfoCon'>
            <div class='name' v-text='item.nickname'></div>
            <div class='describe'>
              <span class='time' v-text='item.publishDate'></span>板块：
              <span class='type' v-text='item.tagName'></span>
              <span class='removeBtn' @click='removePost'>删除</span>
            </div>
          </div>
        </div>
        <div class='text' v-text='item.cont'></div>
        <div class='imgCon' v-if='item.preimg'>
          <div v-for='imgSrc in item.preimg.split("#")' :style="{backgroundImage:'url('+imgSrc+')'}" :key="imgSrc"></div>
        </div>
        <div class='other'><span class='commentBtn'></span><span :class="['iconfont AddPraiseImg',item.like==2&&'active']"></span>{{item.likeNum}}</div>
        <div class='commentCon' v-if='item.commentList.length>0'>
          <div v-for='info in item.commentList' :key="info.userId">
            <span class='name' v-text='info.nickname'></span>:
            <span v-text='info.comment'></span>
            <template v-if='info.replyList.length>0'>
              <template v-for='replyList in info.replyList'>
              <p :key="replyList.userId">
                <!-- <br/> -->
                <span class='name' v-text='replyList.nickname'></span>回复<span class='name' v-text='info.nickname'></span>:<span v-text='replyList.comment'></span>
              </p>
              </template>
            </template>
          </div>
        </div>
      </div>
    <hr />
    </template>
  </div>

</template>
<script>
import { MessageBox,Indicator } from 'mint-ui';
export default {
    name:'',
    props:['forumLists'],
    data () {
        return {
        }
    },
    methods:{
      removePost(){
        MessageBox({
          title: '',
          message: '确定删除吗?',
          showCancelButton: true,
          confirmButtonText:'删除'
        }).then(action => {
          console.log(action);
          if(action==='confirm'){
            Indicator.open({
              text: '正在删除...',
            });
          }
        });
      }
    },
}
</script>

<style lang="scss" scoped>
    *{
      border: none;
      margin: 0;
      padding: 0;
    }
    .forumListCon{
      width:6.9rem;
      margin:0 0.3rem;
      hr{height:1px;}
      .forumList{
        margin:0.36rem 0;
        .userInfo{
          display: flex;
          .headImg{
              width: 0.72rem;
              height: 0.72rem;
              background-size: 0.72rem 0.72rem;
              background-repeat: no-repeat;
              border-radius: 0.72rem; 
              margin-right: 0.24rem;
          }
          .InfoCon{
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content:space-between;
            .name{
                font-size: 0.28rem;
                color: #333333;
            }
            .describe{
                font-size: 0.22rem;
                color: #888888;         
                .time{
                  margin-right:0.08rem;
                }    
                .type{
                  color:#1797c6;
                }       
                .removeBtn{
                  float:right;
                  color:#1797c6;
                }       
            }         
          }
        }
        .text{
          font-size:0.32rem;
          line-height:0.55rem;
          margin:0.24rem 0;
        }
        .imgCon{
          width:100%;
          height:1.8rem;
          display:flex;
          justify-content:space-between;
          margin-bottom:0.24rem;
          div{
            width:2.2rem;
            height:1.8rem;
            background-size: cover;
            background-position:center;
          }
        }
        .other{
          line-height:0.28rem;
          font-size:0.28rem;
          color:#1797c6;
          float:right;
          margin-right:0.3rem;
          .AddPraiseImg{
            font-size:0.28rem;
            margin:0 0.13rem 0 0.34rem;
            &:after{
              content:'\e870';
            }
            &.active:after{
              content:'\e86f';
            }
          }
          .commentBtn{
            display: inline-block;
            width:0.28rem;
            height:0.27rem;
            background:url('../../assets/images/commentImg.png') no-repeat;
            background-size: contain;
          }
        }
        .commentCon{
          width:100%;
          margin-top:0.1rem;
          clear:right;
          background:#f3f3f5;
          font-size:0.3rem;
          color:#333333;
          line-height:0.48rem;
          text-indent: 0.08rem;
          .name{
            color:#1797c6;
          }
        }
      }
    }
</style>




